      arrayFiltrado: [],
      texto: "",
      loaded: false,
    };
  },
  created() {
    this.arrayFiltrado = this.categorias;
  },
  computed: {
    filtro: {
      get() {
        return this.texto;
      },
      set(value) {
        console.log(value);
        this.texto = value;
        value = value.toLowerCase();
        this.arrayFiltrado = this.categorias.filter(
          (nota) => nota.tipo.toLowerCase().indexOf(value) !== -1
        );
      },
    },
  },
};
